@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@400;700&display=swap);


.gol-grid {
    position: fixed;
    z-index: -10;
    width: 100%;
    height: 100vh;
}

.gol-row {
    display: flex;
}

.gol-cell {
    --size: 15px;
    width: var(--size);
    height: var(--size);
    transition: background-color 0.8s ease-in-out;
}

.alive {
    background-color: hsla(var(--gol-alive));
}
:root {

    --primary-base: 20;
    --gol-alpha: 0.05;

    /* Light Mode */
    --bg-light: 0, 0%, 96%;
    --text-light: 0, 0%, 0%;
    --primary-1-light: var(--primary-base), 100%, 90%;
    --primary-2-light: var(--primary-base), 46%, 85%;
    --primary-3-light: var(--primary-base), 40%, 92%;
    --gol-alive-light: 0, 0%, 0%, var(--gol-alpha);

    /* Dark Mode */
    --bg-dark: 245, 17%, 21%;
    --text-dark: 0, 0%, 100%;
    --primary-1-dark: var(--primary-base), 12%, 33%;
    --primary-2-dark: var(--primary-base), 5%, 28%;
    --primary-3-dark: var(--primary-base), 12%, 50%;
    --gol-alive-dark: 245, 100%, 100%, var(--gol-alpha);
}

:root[color-scheme="dark"] {

    --bg: var(--bg-dark);
    --text: var(--text-dark);
    --primary-1: var(--primary-1-dark);
    --primary-2: var(--primary-2-dark);
    --primary-3: var(--primary-3-dark);
    --gol-alive: var(--gol-alive-dark);
    color-scheme: dark;
}

:root[color-scheme="light"] {

    --bg: var(--bg-light);
    --text: var(--text-light);
    --primary-1: var(--primary-1-light);
    --primary-2: var(--primary-2-light);
    --primary-3: var(--primary-3-light);
    --gol-grid: var(--gol-grid-light);
    --gol-alive: var(--gol-alive-light);
    color-scheme: light;
}
html, body, #root, .App {
    height: 100%;
}

body {
    margin: 0;
    background-color: hsl(var(--bg));
    color: hsl(var(--text));
    transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
    font-family: 'Manrope', sans-serif;
}

.theme-switch {
    float: right;
    padding: 15px 20px 15px 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    -webkit-user-select: none;
            user-select: none;
    width: 120px;
}

.float-break {
    clear: both;
}

main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 90%;
}

.main-component {
    position: absolute;
}

.home-page {
    margin-bottom: 5%;
}

.presentation {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.presentation h1 {
    font-size: 50px;
    font-family: 'Outfit', sans-serif;
    text-align: center;
}

.presentation p {
    font-size: 25px;
    max-width: 50%;
}

footer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contact-component {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    position: fixed;
    bottom: 0;
    margin-bottom: 50px;
}

.contact-component .links {
    height: 200px;
    position: absolute;
    display: flex;
    width: 100%;
    justify-content: space-evenly;
}

.link-container {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: 10px;
    translate: 0 100px;
    transition: translate 0.4s ease-in-out, opacity 0.4s ease-in-out, scale 0.2s ease-in-out;
    -webkit-user-select: none;
            user-select: none;
    cursor: pointer;
    z-index: 100;
}

.link-container.above {
    translate: 0 0;
}

.link-container:hover {
    scale: 1.2 1.2;
}

.contact-displayer {
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 100;
}

.contact-displayer p {
    transition: opacity 0.4s ease-in-out;
}

.invisible {
    opacity: 0;
    pointer-events: none;
}

.down-button {
    border: 2px solid hsl(var(--text));
    border-radius: 50%;
    padding: 10px 15px 20px 15px;
    margin: 0;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: scale 0.2s ease-in-out, border 0.2s ease-in-out;
    -webkit-animation: jiggle 2s infinite;
            animation: jiggle 2s infinite;
}

.down-button:hover, .side-button:hover {
    scale: 1.1 1.1;
}

.arrow {
    width: 20px;
	height: 20px;
    border-width: 3px 3px 0 0;
    border-style: solid;
    border-color: var(--text);
    -webkit-transform: translate(0, 50%) rotate(315deg);
            transform: translate(0, 50%) rotate(315deg);
    transition: -webkit-transform 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}

.arrow.down {
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
}

@-webkit-keyframes jiggle {
    0% {
        translate: 0 0;
    }
    50% {
        translate: 0 -10px;
    }
    100% {
        translate: 0 0;
    }
}

@keyframes jiggle {
    0% {
        translate: 0 0;
    }
    50% {
        translate: 0 -10px;
    }
    100% {
        translate: 0 0;
    }
}

.copyright {
    position: fixed;
    bottom: 0;
    margin-bottom: 10px;
}

.nav-button-area {
    position: fixed;
    top: 50%;
    display: flex;
    align-items: center;
    margin: 0 20px;
}

.nav-button-area.right-button {
    right: 0;
}

.nav-button-area.left-button {
    flex-direction: row-reverse;
}

.nav-button-area p {
    margin: 0 10px;
}

.side-button {
    border: 2px solid hsl(var(--text));
    border-radius: 50%;
    padding: 10px;
    display: flex;
    align-items: center;
    cursor: pointer;
    -webkit-user-select: none;
            user-select: none;
    transition: scale 0.2s ease-in-out, border 0.2s ease-in-out;
}

.right-button .side-button {
    -webkit-animation: right-jiggle 2s infinite;
            animation: right-jiggle 2s infinite;
}

.left-button .side-button {
    -webkit-animation: left-jiggle 2s infinite;
            animation: left-jiggle 2s infinite;
}

.left-button svg {
    rotate: 180deg;
}

@-webkit-keyframes right-jiggle {
    0% {
        translate: 5px 0;
    }
    50% {
        translate: -5px 0;
    }
    100% {
        translate: 5px 0;
    }
}

@keyframes right-jiggle {
    0% {
        translate: 5px 0;
    }
    50% {
        translate: -5px 0;
    }
    100% {
        translate: 5px 0;
    }
}

@-webkit-keyframes left-jiggle {
    0% {
        translate: -5px 0;
    }
    50% {
        translate: 5px 0;
    }
    100% {
        translate: -5px 0;
    }
}

@keyframes left-jiggle {
    0% {
        translate: -5px 0;
    }
    50% {
        translate: 5px 0;
    }
    100% {
        translate: -5px 0;
    }
}
@media (max-width: 900px) {
    
    .nav-button-area {
        width: 115px;
        margin: 0;
    }

    .nav-button-area, .nav-button-area.left-button {
        flex-direction: column-reverse;
        align-items: center;
    }

    .nav-button-area p {
        margin: 10px;
    }

    .presentation {
        height: 400px;
        justify-content: start;
    }

    .presentation h1 {
        font-size: 40px;
        margin-bottom: 110px;    
    }
    
    .presentation p {
        font-size: 20px;
        max-width: none;
        width: 70%;
        text-align: center;
    }
}

@media (max-width: 693px) {

    .presentation h1 {
        margin-bottom: 60px;    
    }

    .presentation p {
        font-size: 18px;
    }

    .nav-button-area {
        width: 100px;
    }

    .nav-button-area p, .contact-displayer p {
        font-size: 14px;
    }

    .side-button {
        padding: 5px;
    }

    .side-button svg {
        scale: 0.8;
    }
}

@media (max-width: 600px) {

    .presentation p {
        width: 60%;
    }
}

@media (max-width: 470px) {

    .presentation h1 {
        font-size: 30px;
        margin-bottom: 80px;
    }

    .presentation p {
        font-size: 15px;
        width: 50%;
    }
}
